import React from "react";
import { Link } from "react-router-dom";
import {
  formatDate,
  localStorageencryp,
} from "../../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { postDataNoTImeOut } from "../../../../config/axios.jsx";
import HeadFormHutangLunas from "./HeadFormHutangLunas.jsx";
import * as moment from "moment-timezone";

class FormLaporanHutangLunas extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      dataHutang: [],
      LaporanHutang: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    postDataNoTImeOut("hutang/get/reportlunas", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            isLoading: false,
            export: false,
          });
        } else {
          ToastNotification("success", "Laporan Hutang Lunas Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            LaporanHutang: res.data,
          });
        }
      })
      .catch((err) => {
        this.setState({
          export: false,
          isLoading: false,
        });
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
      });
  }

  render() {
   
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Hutang Lunas</li>
        </ol>
        <h1 className="page-header">Laporan Hutang Lunas</h1>
        <Panel>
          <PanelHeader>Laporan Hutang Lunas</PanelHeader>
          <br />
          {/* Laporan Hutang */}
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFormHutangLunas
                  export={this.state.export}
                  isLoading={this.state.isLoading}
                  LaporanHutang={this.state.LaporanHutang}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
          <br />
          {/* End Tambah Laporan Hutang  */}
        </Panel>
      </div>
    );
  }
}

export default FormLaporanHutangLunas;
